@import '../../global.scss';

.topbar {
    background-color: white;
    color: $mainColor;
    width: 100%;
    height: 70px;
    position: fixed;
    top: 0;
    z-index: 2;

    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left {
            .logo {
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                transition: ease;
            }
        }

        .right {
            a {
                margin-left: 20px;
                color: inherit;
                text-decoration: none;
            }
        }
    }
}